<template>
    <div>
        <p-header></p-header>
        <div style="height: 0.9rem;"></div>
        <div class="content-list">
            <!--  -->
            <div class="classify-left">
                <div class="classify-left-box">
                    <div class="classify-left-bottom">
                        <van-sidebar v-model="activeKey">
                            <van-sidebar-item v-for="item in category" :title="item.cname" @click="cateid(item.id)"
                                              :key="item.id"/>
                        </van-sidebar>
                    </div>
                </div>
            </div>
            <!--  -->
            <!--  -->
            <div class="classify-right">
                <div class="classify-right-center">
                    <div class="right-center-item abc"></div>
                    <div class="right-center-item paixv" style="position: relative">
                        <div class="sort s1"><img src="" alt=""></div>
                        <div class="sort s2" style='display:none'><img src="" alt=""></div>
                        <div class="sort s3" style='display:none'><img src="" alt=""></div>
                    </div>
                    <div class="right-center-item dianji"></div><!--  -->


                    <!--<ul class="hidden-list">-->
                    <!--<li class="hidden-item"></li>-->
                    <!--<li class="hidden-item"></li>-->
                    <!--<li class="hidden-item"></li>-->
                    <!--<li class="hidden-item"></li>-->
                    <!--<li class="hidden-item">/ </li>-->
                    <!--<li class="hidden-item"></li>-->
                    <!--</ul>-->
                </div>
                <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
                    <van-list
                        v-model="loading"
                        :finished="finished"
                        finished-text=""
                        :offset="50"
                        @load="onLoad"
                        :immediate-check="false"
                >
                <div class="classify-right-box" >
                    <!--  -->


                            <div class="container">

                                <div style="height: 1rem"></div>
                                <div id="item-listbox" v-if="goods">
                                    <!-- item -->
                                    <div class="center-bottom-item" v-for="item in goods" :key="item.id">
                                        <router-link :to="{name:'details',query:{id:item.id}}">
                                        <span class="center-item-img">
                                            <div class="center-item-img">
                                                <img v-lazy="imgpath+item.thumb" alt="">
                                            </div>
                                        </span>
                                            <span class="center-item-wz">{{item.gname}}</span>
                                        </router-link>
                                        <div class="center-item-z">
                                            <div class="center-item-price">{{item.sale}}</div>
                                            <div class="center-item-num">
                                                <div class="jian" @click="cutcartgood(item.id,item.sale)"><img src="../../assets/classify/subtract.png" alt="">
                                                </div>
                                                <div class="shuliang">{{$store.getters.goodsnum(item.id)}}</div>
                                                <div class="jia" @click="addcartgood(item)"><img src="../../assets/classify/add.png" alt=""></div>
                                            </div>
                                        </div>
                                    </div>
                                    <!-- item 1  -->
                                </div>
                            </div>
                </div>
                </van-list>

                    </van-pull-refresh>
            </div>
            <!--  -->
        </div>
        <z-footer :active="2"></z-footer>
    </div>
</template>

<script>
    import PHeader from '../../components/p-header/p-header';
    import ZFooter from '../../components/z-footer/z-footer';
    import axios from 'axios';
    import {HOSTNAME, IMGPATH,SUCCESS} from "../../config/base";
    import Vue from 'vue';
    import { Lazyload } from 'vant';
    import {addgood,cutgood} from '../../api/cart';
    Vue.use(Lazyload);
    export default {
        name: "Classify",
        components: {
            PHeader,
            ZFooter
        },
        data() {
            return {
                activeKey: 0,
                category: [],
                goods: [],
                imgpath: IMGPATH,
                id: 0,
                count: 0,
                isLoading: false,
                cid: 0,
                limit: 8,
                current: 1,
                loading: false,
                finished: false,
            }
        },
        methods: {
            onLoad() {
                setTimeout(() => {
                    this.current++;
                    if (this.current <= Math.ceil(this.count / this.limit)) {
                        axios.get(HOSTNAME + '/api/categoods/' + this.cid, {
                            params: {
                                current: this.current,
                                limit: this.limit
                            }
                        })
                            .then(res => {
                                this.goods.push(...res.data.goods);
                                this.count = res.data.count;
                                this.loading = false;
                            })
                            .catch(error => window.console.log(error))
                    }
                    this.loading = false;
                    if (this.current > Math.ceil(this.count / this.limit)) {
                        this.finished = true;
                    }
                },500);
            },
            getcate() {
                axios.get(HOSTNAME + '/api/cate')
                    .then(res => {
                        this.category = res.data.data;
                        this.cid = res.data.data[0].id;
                        this.cateid(this.cid);
                    })
                    .catch(error => window.console.log(error))
            },
            onRefresh() {
                setTimeout(() => {
                    this.isLoading = false;
                }, 500);
            },
            cateid(id) {
                this.current = 1;
                this.goods = [];
                this.count = 1;
                this.finished = false;
                this.cid = id;
                axios.get(HOSTNAME + '/api/categoods/' + id, {
                    params: {
                        'current': this.current,
                        'limit': this.limit
                    }
                })
                    .then(res => {
                        this.goods = res.data.goods;
                        this.count = res.data.count;
                    })
                    // .catch(error => console.log(error))
            },
            addcartgood(item){
                let {sale,id,gname,thumb,num,status}=item;
                addgood('/api/cart',{price:sale,gid:id,gname,thumb,num,status})
                    .then(res=>{
                    let data=res.data;
                    if (data.code===SUCCESS){
                        this.$store.commit('incgoods',{price:sale,gid:id,gname,thumb,num,status});
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                    } else{
                        window.console.log(data.msg);
                    }
                })
            },
            cutcartgood(gid,price){
                cutgood('/api/cutcart',{
                    price:price,
                    gid:gid
                }).then(res=>{
                    let data=res.data;
                    if (data.code===SUCCESS){
                        this.$store.commit('decgoods',gid);
                        this.$store.commit('calcTotal');
                        this.$store.commit('calcPrice');
                    } else{
                        window.console.log(data.msg);
                    }
                })
            },
            goodsinit(){

            }
        },
        beforeMount() {
            this.getcate();
        },

    }
</script>

<style scoped>
    @import url("../../style/classify.css");
    .van-sidebar {
        width: 100%;
        height: 100%;
    }
</style>
